    import { fetchData } from '@/api/action'
import React, { useEffect } from 'react'

    const page = () => {
        const [data, setData] = React.useState<any>(null)

        useEffect(()=> {
            fetchData().then((res) => {
                setData(res)
            })
        }, [])
    return (
        <div>

        </div>
    )
    }

    export default page